import React from "react";
import { Card, Container } from "react-bootstrap";
import { IoIosArrowBack } from "react-icons/io";
import { useNavigate } from "react-router-dom";
import { PATH } from "../../utils/path";
import { SimpleHeader } from "../../components/Layout/SimpleHeader";

export default function PrivacyPolicy() {
  const navigate = useNavigate();
  return (
    <>
      <SimpleHeader />
      <Container>
        <Card className="policies shadow-sm my-3">
          <div className="py-4 px-5">
            <IoIosArrowBack
              size={30}
              className="cursor-pointer"
              style={{ border: "2px solid", borderRadius: "50%" }}
              onClick={() => navigate(PATH.SIGNUP)}
            />
            <h1 className="text-center fw-bold">Privacy Policy</h1>
            <p>
              In order to receive information about your Personal Data, the
              purposes and the parties the Data is shared with, contact the
              Owner.
            </p>
            <div>
              <h5 className="fw-bold">Owner and Data Controller</h5>
              <p className="mb-0">FamilyDoc Medical Services Limited</p>
              <p>77 Camden Street Lower, Dublin 2, D02 XE80</p>
              <p>
                <span className="fw-bold">Owner contact email:</span>{" "}
                contact@familydoc247.ie
              </p>
            </div>
            <div>
              <h5 className="fw-bold">Types of Data collected</h5>
              <p>
                The owner does not provide a list of Personal Data types
                collected.
              </p>
              <p>
                Complete details on each type of Personal Data collected are
                provided in the dedicated sections of this privacy policy or by
                specific explanation texts displayed prior to the Data
                collection. Personal Data may be freely provided by the User,
                or, in case of Usage Data, collected automatically when using
                familydoc247.ie. Unless specified otherwise, all Data requested
                by familydoc247.ie is mandatory and failure to provide this Data
                may make it impossible for familydoc247.ie to provide its
                services. In cases where familydoc247.ie specifically states
                that some Data is not mandatory, Users are free not to
                communicate this Data without consequences to the availability
                or the functioning of the Service. Users who are uncertain about
                which Personal Data is mandatory are welcome to contact the
                Owner. Any use of Cookies – or of other tracking tools – by
                familydoc247.ie or by the owners of third-party services used by
                familydoc247.ie serves the purpose of providing the Service
                required by the User, in addition to any other purposes
                described in the present document and in the Cookie Policy, if
                available.
              </p>
              <p>
                Users are responsible for any third-party Personal Data
                obtained, published or shared through familydoc247.ie.
              </p>
            </div>
            <div>
              <h5 className="fw-bold">Mode and place of processing the Data</h5>
              <h6 className="fw-bold">Methods of processing</h6>
              <p>
                The Owner takes appropriate security measures to prevent
                unauthorised access, disclosure, modification, or unauthorised
                destruction of the Data. The Data processing is carried out
                using computers and/or IT enabled tools, following
                organisational procedures and modes strictly related to the
                purposes indicated. In addition to the Owner, in some cases, the
                Data may be accessible to certain types of persons in charge,
                involved with the operation of familydoc247.ie (administration,
                sales, marketing, legal, system administration) or external
                parties (such as third-party technical service providers, mail
                carriers, hosting providers, IT companies, communications
                agencies) appointed, if necessary, as Data Processors by the
                Owner. The updated list of these parties may be requested from
                the Owner at any time.
              </p>
              <h6 className="fw-bold">Place</h6>
              <p>
                The Data is processed at the Owner's operating offices and in
                any other places where the parties involved in the processing
                are located.
              </p>
              <p>
                Depending on the User's location, data transfers may involve
                transferring the User's Data to a country other than their own.
                To find out more about the place of processing of such
                transferred Data, Users can check the section containing details
                about the processing of Personal Data.
              </p>
              <h6 className="fw-bold">Retention time</h6>
              <p>
                Unless specified otherwise in this document, Personal Data shall
                be processed and stored for as long as required by the purpose
                they have been collected for and may be retained for longer due
                to applicable legal obligation or based on the Users’ consent.
              </p>
            </div>
            <div>
              <h5 className="fw-bold">
                Information on opting out of interest-based advertising
              </h5>
              <p>
                In addition to any opt-out feature provided by any of the
                services listed in this document, Users may learn more on how to
                generally opt out of interest-based advertising within the
                dedicated section of the Cookie Policy.
              </p>
            </div>
            <div>
              <h5 className="fw-bold">Cookie Policy</h5>
              <p>
                Familydoc247.ie uses Trackers. To learn more, the User may
                consult the Cookie Policy.
              </p>
            </div>
            <div>
              <h5 className="fw-bold">Further Information for Users</h5>
              <h6 className="fw-bold">Legal basis of processing</h6>
              <p>
                The Owner may process Personal Data relating to Users if one of
                the following applies:
              </p>
              <ul>
                <li>
                  Users have given their consent for one or more specific
                  purposes. Note: Under some legislations the Owner may be
                  allowed to process Personal Data until the User objects to
                  such processing (“opt-out”), without having to rely on consent
                  or any other of the following legal bases. This, however, does
                  not apply, whenever the processing of Personal Data is subject
                  to European data protection law;
                </li>
                <li>
                  provision of Data is necessary for the performance of an
                  agreement with the User and/or for any pre-contractual
                  obligations thereof;
                </li>
                <li>
                  processing is necessary for compliance with a legal obligation
                  to which the Owner is subject;
                </li>
                <li>
                  processing is related to a task that is carried out in the
                  public interest or in the exercise of official authority
                  vested in the Owner;
                </li>
                <li>
                  processing is necessary for the purposes of the legitimate
                  interests pursued by the Owner or by a third party.
                </li>
              </ul>
              <p>
                In any case, the Owner will gladly help to clarify the specific
                legal basis that applies to the processing, and in particular
                whether the provision of Personal Data is a statutory or
                contractual requirement, or a requirement necessary to enter
                into a contract.
              </p>
            </div>
            <div>
              <h5 className="fw-bold">
                Further information about retention time
              </h5>
              <p>
                Unless specified otherwise in this document, Personal Data shall
                be processed and stored for as long as required by the purpose
                they have been collected for and may be retained for longer due
                to applicable legal obligation or based on the Users’ consent.
              </p>
              <p>Therefore:</p>
              <ul>
                <li>
                  Personal Data collected for purposes related to the
                  performance of a contract between the Owner and the User shall
                  be retained until such contract has been fully performed.
                </li>
                <li>
                  Personal Data collected for the purposes of the Owner’s
                  legitimate interests shall be retained as long as needed to
                  fulfill such purposes. Users may find specific information
                  regarding the legitimate interests pursued by the Owner within
                  the relevant sections of this document or by contacting the
                  Owner.
                </li>
              </ul>
              <p>
                The Owner may be allowed to retain Personal Data for a longer
                period whenever the User has given consent to such processing,
                as long as such consent is not withdrawn. Furthermore, the Owner
                may be obliged to retain Personal Data for a longer period
                whenever required to do so for the performance of a legal
                obligation or upon order of an authority.
              </p>
              <p>
                Once the retention period expires, Personal Data shall be
                deleted. Therefore, the right of access, the right to erasure,
                the right to rectification and the right to data portability
                cannot be enforced after expiration of the retention period.
              </p>
            </div>
            <div>
              <h5 className="fw-bold">The rights of Users</h5>
              <p>
                Users may exercise certain rights regarding their Data processed
                by the Owner.
              </p>
              <p>
                In particular, Users have the right to do the following, to the
                extent permitted by law:
              </p>
              <ul>
                <li>
                  <b>Withdraw their consent at any time.</b> Users have the
                  right to withdraw consent where they have previously given
                  their consent to the processing of their Personal Data.
                </li>
                <li>
                  <b>Object to processing of their Data.</b> Users have the
                  right to object to the processing of their Data if the
                  processing is carried out on a legal basis other than consent.
                </li>
                <li>
                  <b>Access their Data.</b> Users have the right to learn if
                  Data is being processed by the Owner, obtain disclosure
                  regarding certain aspects of the processing and obtain a copy
                  of the Data undergoing processing.
                </li>
                <li>
                  <b>Verify and seek rectification.</b> Users have the right to
                  verify the accuracy of their Data and ask for it to be updated
                  or corrected.
                </li>
                <li>
                  <b>Restrict the processing of their Data.</b> Users have the
                  right to restrict the processing of their Data. In this case,
                  the Owner will not process their Data for any purpose other
                  than storing it.
                </li>
                <li>
                  <b>Have their Personal Data deleted or otherwise removed.</b>{" "}
                  Users have the right to obtain the erasure of their Data from
                  the Owner.
                </li>
                <li>
                  <b>
                    Receive their Data and have it transferred to another
                    controller.
                  </b>{" "}
                  Users have the right to receive their Data in a structured,
                  commonly used and machine readable format and, if technically
                  feasible, to have it transmitted to another controller without
                  any hindrance.
                </li>
                <li>
                  <b>Lodge a complaint.</b> Users have the right to bring a
                  claim before their competent data protection authority.
                </li>
              </ul>
              <p>
                Users are also entitled to learn about the legal basis for Data
                transfers abroad including to any international organization
                governed by public international law or set up by two or more
                countries, such as the UN, and about the security measures taken
                by the Owner to safeguard their Data.
              </p>
            </div>
            <div>
              <h6 className="fw-bold">
                Details about the right to object to processing
              </h6>
              <p>
                Where Personal Data is processed for a public interest, in the
                exercise of an official authority vested in the Owner or for the
                purposes of the legitimate interests pursued by the Owner, Users
                may object to such processing by providing a ground related to
                their particular situation to justify the objection.
              </p>
              <p>
                Users must know that, however, should their Personal Data be
                processed for direct marketing purposes, they can object to that
                processing at any time, free of charge and without providing any
                justification. Where the User objects to processing for direct
                marketing purposes, the Personal Data will no longer be
                processed for such purposes. To learn whether the Owner is
                processing Personal Data for direct marketing purposes, Users
                may refer to the relevant sections of this document.
              </p>
            </div>
            <div>
              <h6 className="fw-bold">How to exercise these rights</h6>
              <p>
                Any requests to exercise User rights can be directed to the
                Owner through the contact details provided in this document.
                These requests can be exercised free of charge and will be
                answered by the Owner as early as possible and always within one
                month, providing Users with the information required by law. Any
                rectification or erasure of Personal Data or restriction of
                processing will be communicated by the Owner to each recipient,
                if any, to whom the Personal Data has been disclosed unless this
                proves impossible or involves disproportionate effort. At the
                Users’ request, the Owner will inform them about those
                recipients.
              </p>
            </div>
            <div>
              <h5 className="fw-bold">
                Additional information about Data collection and processing
              </h5>
              <span>
                <h6 className="fw-bold">Legal action</h6>
                <p>
                  The User's Personal Data may be used for legal purposes by the
                  Owner in Court or in the stages leading to possible legal
                  action arising from improper use of familydoc247.ie or the
                  related Services. The User declares to be aware that the Owner
                  may be required to reveal personal data upon request of public
                  authorities.
                </p>
              </span>
              <span>
                <h6 className="fw-bold">
                  Additional information about User's Personal Data
                </h6>
                <p>
                  In addition to the information contained in this privacy
                  policy, familydoc247.ie may provide the User with additional
                  and contextual information concerning particular Services or
                  the collection and processing of Personal Data upon request.
                </p>
              </span>
              <span>
                <h6 className="fw-bold">System logs and maintenance</h6>
                <p>
                  For operation and maintenance purposes, familydoc247.ie and
                  any third-party services may collect files that record
                  interaction with familydoc247.ie (System logs) or use other
                  Personal Data (such as the IP Address) for this purpose.
                </p>
              </span>
              <span>
                <h6 className="fw-bold">
                  Information not contained in this policy
                </h6>
                <p>
                  More details concerning the collection or processing of
                  Personal Data may be requested from the Owner at any time.
                  Please see the contact information at the beginning of this
                  document.
                </p>
              </span>
              <span>
                <h6 className="fw-bold">Changes to this privacy policy</h6>
                <p>
                  The Owner reserves the right to make changes to this privacy
                  policy at any time by notifying its Users on this page and
                  possibly within familydoc247.ie and/or - as far as technically
                  and legally feasible - sending a notice to Users via any
                  contact information available to the Owner. It is strongly
                  recommended to check this page often, referring to the date of
                  the last modification listed at the bottom.
                </p>
              </span>
              <p>
                Should the changes affect processing activities performed on the
                basis of the User’s consent, the Owner shall collect new consent
                from the User, where required.
              </p>
            </div>
            <div>
              <h2 className="fw-bold">Definitions and legal references</h2>
              <div>
                <h5 className="fw-bold">Personal Data (or Data)</h5>
                <p>
                  Any information that directly, indirectly, or in connection
                  with other information — including a personal identification
                  number — allows for the identification or identifiability of a
                  natural person.
                </p>
              </div>
              <div>
                <h5 className="fw-bold">Usage Data</h5>
                <p>
                  Information collected automatically through familydoc247.ie
                  (or third-party services employed in familydoc247.ie), which
                  can include: the IP addresses or domain names of the computers
                  utilised by the Users who use familydoc247.ie, the URI
                  addresses (Uniform Resource Identifier), the time of the
                  request, the method utilised to submit the request to the
                  server, the size of the file received in response, the
                  numerical code indicating the status of the server's answer
                  (successful outcome, error, etc.), the country of origin, the
                  features of the browser and the operating system utilised by
                  the User, the various time details per visit (e.g., the time
                  spent on each page within the Application) and the details
                  about the path followed within the Application with special
                  reference to the sequence of pages visited, and other
                  parameters about the device operating system and/or the User's
                  IT environment.
                </p>
              </div>
              <div>
                <h5 className="fw-bold">User</h5>
                <p>
                  The individual using familydoc247.ie who, unless otherwise
                  specified, coincides with the Data Subject.
                </p>
              </div>
              <div>
                <h5 className="fw-bold">Data Subject</h5>
                <p>The natural person to whom the Personal Data refers.</p>
              </div>
              <div>
                <h5 className="fw-bold">Data Processor (or Processor)</h5>
                <p>
                  The natural or legal person, public authority, agency or other
                  body which processes Personal Data on behalf of the
                  Controller, as described in this privacy policy.
                </p>
              </div>
              <div>
                <h5 className="fw-bold">Data Controller (or Owner)</h5>
                <p>
                  The natural or legal person, public authority, agency or other
                  body which, alone or jointly with others, determines the
                  purposes and means of the processing of Personal Data,
                  including the security measures concerning the operation and
                  use of familydoc247.ie. The Data Controller, unless otherwise
                  specified, is the Owner of familydoc247.ie.
                </p>
              </div>
              <div>
                <h5 className="fw-bold">
                  familydoc247.ie (or this Application)
                </h5>
                <p>
                  The means by which the Personal Data of the User is collected
                  and processed.
                </p>
              </div>
              <div>
                <h5 className="fw-bold">Service</h5>
                <p>
                  The service provided by familydoc247.ie as described in the
                  relative terms (if available) and on this site/application.
                </p>
              </div>
              <div>
                <h5 className="fw-bold">European Union (or EU)</h5>
                <p>
                  Unless otherwise specified, all references made within this
                  document to the European Union include all current member
                  states to the European Union and the European Economic Area.
                </p>
              </div>
              <div>
                <h5 className="fw-bold">Legal information</h5>
                <p>
                  This privacy policy relates solely to familydoc247.ie, if not
                  stated otherwise within this document.
                </p>
              </div>
            </div>
          </div>
        </Card>
      </Container>
    </>
  );
}
